import * as React from "react";

import {FBFieldWithPriceBase, FBFieldWithPriceBaseState} from "FormBuilder/Fields/Core/FBFieldWithPriceBase";
import {FBFieldBaseProps} from "FormBuilder/Fields/Core/FBFieldBase";
import {PriceTypeEnum} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Options";
import {GetModel} from "FormBuilder/Events/GetModel";
import {FieldTypeEnum} from "FormBuilder/Fields/Core/FBFieldBase.options";
import {FBHiddenModel} from "../../../Pro/Fields/Fields/FBHidden/FBHidden.Model";
import {FBHiddenOptions} from "../../../Pro/Fields/Fields/FBHidden/FBHidden.Options";
import {GetFieldOptions} from "FormBuilder/Events/GetFieldOptions";
import {GetField} from "FormBuilder/Events/GetField";
import {FBHidden} from "../../../Pro/Fields/Fields/FBHidden/FBHidden";
import {FBMaskedFieldOptions} from "./FBMaskedField.options";
import {FBMaskedFieldModel} from "./FBMaskedField.Model";
import {EventManager} from "shared/core/Events/EventManager";

let MaskedInput=require('react-input-mask');

export class FBMaskedField extends FBFieldWithPriceBase<FBMaskedFieldProps,FBMaskedFieldState>{

    static defaultProps: Partial<FBMaskedFieldProps> = {

    };

    constructor(props: FBMaskedFieldProps) {
        super(props);
    }

    componentWillReceiveProps(nextProps: Readonly<FBMaskedFieldProps>, nextContext: any): void {

        if(this.Model.Parent.IsDesign)
        {
            this.Model.Text=this.Model.Options.DefaultText;
            this.Model.Quantity=this.Model.Options.QuantityDefaultValue==''?"": this.Model.ParseNumber(this.Model.Options.QuantityDefaultValue);
        }

    }

    SubRender() {
        if(this.props.Model.Options.ShowQuantitySelector&&this.props.Model.Options.PriceType!=PriceTypeEnum.quantity)
        {
            return(
            this.props.Model.Options.QuantityPosition == "bottom" || this.props.Model.Options.QuantityPosition == "right" ?
                <React.Fragment>
                    {this.GetInput(this.props.Model.Options.QuantityPosition)}
                    {this.GetQuantityInput()}

                        {this.GetPriceOrRequiredBox()}

                </React.Fragment>:
                <React.Fragment>
                    {this.GetQuantityInput()}
                    {this.GetInput(this.props.Model.Options.QuantityPosition)}

                        {this.GetPriceOrRequiredBox()}

                </React.Fragment>
            )
        }
        return(
            <React.Fragment>
                {this.GetInput('')}

                    {this.GetPriceOrRequiredBox()}

            </React.Fragment>
        )


    }






    GetQuantityInput(){
        return(
            <div className={'rnTextFieldQuantity '+ this.props.Model.Options.QuantityPosition}>
                <div className={'rednaoLabel'}>
                    <label style={{fontWeight:'bold'}}>{this.props.Model.Options.QuantityLabel}</label>
                </div>
                <input className={'rnInputQuantity'} placeholder={this.Model.Options.QuantityPlaceholder} style={{width:'100%'}} type={'text'} value={this.Model.Quantity}
                       onChange={x=>{this.Model.Quantity=this.Model.ParseNumber(x.target.value as any);this.Model.Refresh()}}/>
            </div>

        )
    }

    GetInput(additionalClassNames:string){
        return (
            <div className={'rnTextFieldInput '+ additionalClassNames}>
                {this.GetLabel()}
                <MaskedInput maskChar={this.Model.Options.MaskChar}  mask={this.Model.Options.Mask} className={'rnInputPrice'} placeholder={this.props.Model.Options.Placeholder} style={{width:'100%'}} type={'text'} value={this.Model.Text} onChange={x=>this.OnChange(x)}/>
            </div>

        );
    }






    private OnChange(x: React.ChangeEvent<HTMLInputElement>) {
        if(this.Model.Text.trim()!='')
            this.Model.RemoveError('required');
        console.log(x.target.value);

        this.Model.Text=x.target.value;
        this.Model.Quantity=x.target.value.trim().length>0&&this.Model.Quantity==0?1:this.Model.Quantity;
        this.Model.FireValueChanged();


    }
}


//#region ----------------------------------------------------"State and props"------------------------------------------------------------------------------------
interface FBMaskedFieldProps extends FBFieldBaseProps{
    Model:FBMaskedFieldModel;
}

interface FBMaskedFieldState extends FBFieldWithPriceBaseState{

}

EventManager.Subscribe(GetModel,(event)=>{
    if(event.Options.Type==FieldTypeEnum.Masked)
        return new FBMaskedFieldModel(event.Options as FBMaskedFieldOptions,event.parent).Initialize(event.PreviousData);
});


EventManager.Subscribe(GetFieldOptions,(event)=>{
    if(event.Type==FieldTypeEnum.Masked)
        return new FBMaskedFieldOptions();
});

EventManager.Subscribe(GetField,(event)=>{
    if(event.Model.Options.Type==FieldTypeEnum.Masked)
        return <FBMaskedField Model={event.Model as FBMaskedFieldModel} />
});