
import {ModelWithParent} from "FormBuilder/Core/Models/ModelWithParenth";
import {FBColumnModel} from "FormBuilder/Core/FBColumn.Model";
import {FBFieldWithPriceBaseModel} from "FormBuilder/Fields/Core/FBFieldWithPriceBase.Model";
import {FBMaskedFieldOptions} from "./FBMaskedField.options";


export class FBMaskedFieldModel extends FBFieldWithPriceBaseModel<FBMaskedFieldOptions,FBColumnModel>
{
    public Text:string;


    constructor(Options: FBMaskedFieldOptions, Parent: FBColumnModel) {
        super(Options, Parent);

    }

    InitializeStartingValues(previousData?: any) {
        this.Text=this.GetPreviousDataProperty('Value', this.Options.DefaultText);
        this.Quantity=this.GetPreviousDataProperty( 'Quantity',this.Options.QuantityDefaultValue==''?"": this.ParseNumber(this.Options.QuantityDefaultValue));
    }

    protected InternalSerialize(obj: any) {
        super.InternalSerialize(obj);
        obj.Value=this.GetValue();
    }


    GetDynamicFieldNames(): string[] {
        return [
          'FBMaskedField'
        ];
    }

    GetStoresInformation(): boolean {
        return true;
    }

    GetIsUsed()
    {
        return this.GetValue()!='';
    }

    GetValue() {
        if(this.Text.trim().indexOf(this.Options.MaskChar)!=-1)
            return '';
        return this.Text.trim();
    }
}